*{
    padding: 0;
    margin: 0;
}
ul, ol { list-style:none; }
a{
  text-decoration: none;
}
.div1{
  margin-top: 100px;
  margin-left:10px;
  height: 1800px;
}
.div1 div{ 
  border:3px solid white;
  position: relative;
  width:45%;
  height: 300px;
  margin-left: 5px;
  margin-top: 30px;
  float: left;
}
.span1{
  text-align: center;
  margin-bottom:10px;
  margin-left:3px;
  color:#333;
  display: block;
}
.span2{
  text-align: center;
  color:#ff5500;
  height: 30px;
  position: absolute;
  left: 20%;
}
